//

@mixin list-style-none {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin fieldset-style-none {
  margin: 0;
  padding: 0;

  border: none;
}

@mixin zero-margin-padding {
  margin: 0;
  padding: 0;
}

@mixin container-flex {
  display: flex;
  justify-content: space-between;
}

@mixin link-decoration-none {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
