.modal {
  display: none;
  position: fixed;
  top: 120px;
  left: 50%;
  margin: 0;
  margin-left: -160px;
  padding: 0;
  width: 320px;
  z-index: 3;
  background-color: #ffffff;
  box-shadow: 0 7px 15px rgba(0, 1, 1, 0.15);
  border-radius: 5px; }

.modal__header {
  margin: 0;
  padding: 0;
  padding-top: 40px;
  padding-bottom: 26px;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  text-align: center; }

.modal__description {
  margin: 0;
  padding: 0;
  margin-bottom: 39px;
  padding-right: 10px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-align: center; }

.modal__description-success {
  margin-left: 19px;
  margin-right: 15px;
  margin-bottom: 32px;
  padding-left: 11px;
  padding-right: 15px; }

.modal__button-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 163px;
  background-color: #f2f2f2; }

.modal--failure {
  height: 289px; }

.modal--success {
  height: 340px; }

.modal--show {
  display: block;
  animation-name: appear;
  animation-duration: 500ms;
  animation-timing-function: linear; }

.modal--error {
  animation-name: shake;
  animation-duration: 500ms;
  animation-timing-function: linear; }

@keyframes appear {
  0% {
    transform: translateX(-200%); }
  50% {
    transform: translateX(-100%); }
  100% {
    transform: translateX(0%); } }

@keyframes shake {
  0% {
    transform: translateX(0); }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-5px); }
  20%,
  40%,
  60%,
  80%,
  100% {
    transform: translateX(5px); } }

