/*
  $Раздел загрузки
*/
.app-download {
  margin-top: -79px;
  margin-bottom: 40px;
  padding-top: 247px;
  min-height: 462px;

  background-color: #2a3a4a;
  background-image: url("../img/iphone-hand-mobile@1x.png"), url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat, no-repeat;
  background-size: 260px 286px, 100% 455px;
  background-position: -1px 180px, 0 4%;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/iphone-hand-mobile@2x.png"),
      url("../img/bg-back-mobile@2x.jpg");
  }
}

.app-download__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 72%;
  width: 100%;

  z-index: 1;
}

.app-download__title {
  display: none;

  text-align: center;
}

.app-download__button {
  margin-top: 17px;
  margin-right: 15px;
  margin-bottom: 20px;
  margin-left: 15px;
  padding-top: 16px;
  padding-right: 35px;
  padding-bottom: 16px;
  padding-left: 35px;

  color: $top-menu-text-color;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;

  border: 3px solid #ffffff;
  border-radius: 50px;

  &:hover {
    color: $contacts__link-color;
    background-color: #ffffff;
  }

  &:active {
    color: opacify($contacts__link-color, 0.3);
    background-color: #ffffff;
  }
}

.app-download__list {
  @include list-style-none;

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 5px;
  padding-top: 12px;
  min-width: 240px;
}

.app-download__description {
  margin-bottom: 29px;
  padding-right: 20px;
  padding-left: 20px;
  width: 90%;

  color: $top-menu-text-color;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .app-download {
    margin-top: -21%;
    padding-top: 117px;
    padding-left: 0;
    padding-right: 20px;

    background-color: #2a3a4a;

    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
    }
  }

  .app-download__wrapper {
    margin-top: 89px;
    width: 340px;

    background-color: transparent;
  }

  .app-download__title {
    @include zero-margin-padding;

    display: block;
    margin-bottom: 34px;
    width: 60%;

    color: $top-menu-text-color;
    font-size: 40px;
    font-weight: 300;
  }

  .app-download__button {
    @include zero-margin-padding;
    margin-bottom: 35px;
    padding-top: 15px;
    padding-right: 46px;
    padding-left: 46px;
    padding-bottom: 16px;
  }

  .app-download__list {
    max-width: 205px;
    margin-bottom: 14px;
  }

  .app-download__description {
    padding-right: 15px;
    padding-left: 15px;

    font-size: 18px;
  }
}

@media (min-width: $desktop-width) {
  .app-download {
    @include zero-margin-padding;
    margin-top: -22%;
    margin-bottom: 56px;
    padding-top: 17%;

    background-color: #2a3a4a;
    background-image: url("../img/bg-back-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-back-desktop@2x.jpg");
    }
  }

  .app-download__wrapper {
    @include zero-margin-padding;
    padding-top: 167px;
    padding-left: 0;
    padding-bottom: 93px;
    width: $tablet-width / 2;
    align-items: flex-start;

    background-color: transparent;
  }

  .app-download__title {
    @include zero-margin-padding;

    margin-bottom: 52px;
    width: 91%;

    font-size: 50px;
    line-height: 60px;
  }

  .app-download__button {
    @include zero-margin-padding;

    align-self: flex-start;
    margin-left: 0;
    margin-bottom: 55px;
    padding-bottom: 17px;
    padding-left: 29px;
    padding-right: 29px;
    padding-top: 14px;

    font-size: 21px;
    font-weight: 700;
    line-height: 18px;
  }

  .app-download__list {
    justify-content: space-between;
    align-self: center;
    align-items: flex-end;
    box-sizing: border-box;
    margin-bottom: 36px;
    padding-left: 0;
    padding-top: 10px;
    padding-right: 32px;
    padding-bottom: 1px;
    min-width: 238px;
  }

  .app-download__description {
    padding-left: 0;
    padding-right: 18px;
  }
}
