/*
  $Раздел особенностей
*/

.features {
  margin-bottom: 42px;

  background: linear-gradient(175deg, transparent 21%, $features-list-bg-color 0%);
}

.features__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 788px;

  overflow: hidden;
}

.features__list {
  @include list-style-none;

  margin-bottom: 9px;
  padding-top: 86px;
  width: 100%;

  text-align: center;

  order: 1;
}

.features__slogan {
  margin-top: 0;
  margin-right: 55px;
  margin-bottom: 32px;
  margin-left: 55px;

  background-color: transparent;
  color: $top-menu-bg-color;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
}

.features__list-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 31px;
}

.features__list-item-phone {
  display: none;
}

.features__list-item:nth-child(3) .features__list-item-phone {
  display: block;
  width: 157px;
  min-height: 320px;

  order: 2;

  background-image: url("../img/iphone-screen-mobile@1x.png");
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/iphone-screen-mobile@2x.png");
  }
}

.features__list-item-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
}

.features__list-item:nth-child(3) .features__list-item-card {
  padding-bottom: 40px;
}

.features__list-item-title {
  @include zero-margin-padding;

  padding-bottom: 13px;

  color: $features-title-text-color;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.features__list-item-description {
  @include zero-margin-padding;

  padding-right: 11px;
  padding-left: 11px;
  width: 100%;

  color: $top-menu-bg-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.features__facts {
  display: none;
}

.features__iphones-screen-list {
  @include list-style-none;

  display: flex;
  flex-direction: column;

  order: 2;
}

@media (min-width: $tablet-width) {
  .features {
    margin-bottom: 95px;

    background: linear-gradient(176deg, transparent 34%, #f2f2f2 0%);
    background-size: 100% 67%;
    background-repeat: no-repeat;
  }

  .features__wrapper {
    position: relative;
    flex-direction: row;
    margin-bottom: 23px;

    overflow: unset;
  }

  .features__slogan {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
    padding-top: 12px;

    font-size: 40px;
    font-weight: 300;
    text-align: center;
  }

  .features__slogan--second-part {
    padding-top: 4px;
    padding-left: 100px;
    padding-right: 100px;
  }

  .features__list-item:nth-child(3) .features__list-item-phone {
    display: block;
    position: absolute;
    top: 49px;
    left: -129px;
    width: 268px;
    min-height: 481px;

    order: 1;

    background-image: url("../img/iphone-screen-3-tablet@1x.png");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/iphone-screen-3-tablet@2x.png");
    }
  }

  .features__list-item-card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 90%;
  }

  .features__list-item:nth-child(3) .features__list-item-card {
    order: 2;
    margin-bottom: 0;
    padding-bottom: 0;
  }

  .features__list {
    @include zero-margin-padding;
    order: 2;
    position: relative;

    margin-left: 150px;
    padding-top: 119px;
    padding-left: 161px;
    padding-right: 0;
    padding-bottom: 62px;
    width: auto;
  }

  .features__list-item {
    margin-bottom: 30px;

    text-align: left;
  }

  .features__title {
    margin-left: 27px;
    padding-bottom: 10px;
  }

  .features__list-item-title {
    @include zero-margin-padding;
    margin-bottom: 5px;

    font-size: 20px;
    font-weight: 700;
    line-height: 32px;
  }

  .features__list-item-description {
    @include zero-margin-padding;
    width: 100%;

    font-size: 14px;
    line-height: 25px;
    text-align: left;
  }

  .features__facts {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-left: 20px;
    padding-top: 6px;

    background-color: #ffffff;
  }

  // Циферблат часов
  .features__timer {
    margin: 0;
    position: relative;
  }

  .features__timer::before {
    display: block;
    position: absolute;
    content: "";
    width: 175px;
    height: 175px;
    top: 54px;
    left: 16px;
    box-sizing: content-box;

    border: 15px solid #d22856;
    border-radius: 50%;
  }

  .features__timer::after {
    display: inline-block;
    position: absolute;
    content: "24";
    top: 76px;
    left: 63px;

    color: #d22856;
    font-size: 96px;
    font-weight: 700;
    line-height: 149.47px;
  }

  .features__facts-wrapper {
    margin-left: 70px;
    padding-top: 32px;
  }

  .features__facts-title {
    margin-top: 5px;
    margin-bottom: 10px;
    padding-right: 158px;

    font-size: 30px;
    line-height: 37px;
  }

  .features__facts-link {
    margin-top: 0;
    color: #d22856;

    text-decoration: underline;
  }

  .features__facts-description {
    margin-top: 5px;
    margin-bottom: 13px;
    padding-right: 35px;

    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
  }
}

@media (min-width: $desktop-width) {
  .features {
    margin-bottom: 72px;

    background: linear-gradient(175deg, transparent 26%, #f2f2f2 0%);
  }

  .features__wrapper {
    flex-direction: column;
    align-items: inherit;
    margin-bottom: 64px;
  }

  .features__slogan {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
    padding-top: 12px;
    padding-left: 71px;
    padding-right: 71px;

    font-size: 50px;
    font-weight: 300;
    text-align: center;
  }

  .features__slogan--second-part {
    margin-bottom: 78px;
    padding-top: 4px;
    padding-left: 113px;
    padding-right: 114px;
  }

  .features__list {
    @include zero-margin-padding;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 62px;
    width: 100%;
  }

  .features__list-item-phone {
    @include zero-margin-padding;

    display: block;
    order: 1;
    margin-bottom: 57px;
    width: 292px;
    min-height: 302px;

    background-repeat: no-repeat;
    background-size: cover;
  }

  .features__list-item {
    margin-bottom: 0;
    margin-right: 20px;
    width: 300px;

    &:nth-child(1n) .features__list-item-phone {
      background-image: url("../img/iphone-screen-1-desktop@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/iphone-screen-1-desktop@2x.png");
      }
    }

    &:nth-child(2n) .features__list-item-phone {
      background-image: url("../img/iphone-screen-2-desktop@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/iphone-screen-2-desktop@2x.png");
      }
    }

    &:nth-child(3n) .features__list-item-phone {
      align-self: flex-end;
      position: static;
      margin-left: 0;
      width: 292px;
      min-height: 302px;
      background-image: url("../img/iphone-screen-3-desktop@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/iphone-screen-3-desktop@2x.png");
      }
    }

    &:nth-child(3) .features__title {
      padding-left: 42px;
    }

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  .features__list-item-card {
    @include zero-margin-padding;
    width: 83%;
    order: 2;

    text-align: center;
  }

  .features__title {
    @include zero-margin-padding;

    margin-bottom: 28px;
    padding-left: 1px;
    padding-right: 35px;

    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    text-align: center;
  }

  .features__list-item-description {
    @include zero-margin-padding;

    width: 100%;

    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
  }

  .features__timer {
    margin-top: 0;
    margin-left: 0;
    margin-bottom: 0;

    & img {
      width: 298px;
      height: 345px;
    }
  }

  .features__timer::before {
    width: 222px;
    height: 222px;
    top: 70px;
    left: 24px;
  }

  .features__timer::after {
    content: "24";

    top: 114px;
    left: 80px;

    font-size: 120px;
    font-weight: 700;
    line-height: 149.47px;
  }

  .features__facts-wrapper {
    @include zero-margin-padding;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 68px;
    padding-top: 87px;
    padding-bottom: 50px;
    width: 100%;
  }

  .features__facts {
    @include zero-margin-padding;
    padding-bottom: 62px;

    background-color: #f2f2f2;
  }

  .features__list-item-title {
    @include zero-margin-padding;

    margin-bottom: 25px;
    padding-left: 1%;
    padding-right: 0;
    width: 97%;

    color: #000000;
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
  }

  .features__facts-title {
    margin-top: 5px;
    margin-bottom: 21px;
    padding-right: 158px;
  }

  .features__facts-description {
    margin-top: 0;
    margin-bottom: 32px;

    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
  }

  .features__facts-link {
    color: #d22856;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    text-decoration: underline;
  }
}
